/**
 * 
 * 
 * - 
 * - 
 * - 
 */

import React, { useState, useEffect } from 'react';
import { Card, Row, Col, Statistic, List, Typography, Spin } from 'antd';
import { 
  ShoppingOutlined, 
  MenuOutlined, 
  UserOutlined,
  TrophyOutlined 
} from '@ant-design/icons';
import { materialsAPI, menusAPI } from '../services/api';

const { Title } = Typography;

/**
 * 
 */
const Dashboard = () => {
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    totalMaterials: 0,
    totalMenus: 0,
    recentMenus: [],
    recentMaterials: [],
  });

  /**
   * 
   */
  useEffect(() => {
    const loadData = async () => {
      try {
        setLoading(true);
        
        // 
        const [materials, menus] = await Promise.all([
          materialsAPI.getMaterials({ limit: 10 }),
          menusAPI.getMenus({ limit: 10 }),
        ]);

        setStats({
          totalMaterials: materials.length,
          totalMenus: menus.length,
          recentMenus: menus.slice(0, 5),
          recentMaterials: materials.slice(0, 5),
        });
      } catch (error) {
        console.error(':', error);
      } finally {
        setLoading(false);
      }
    };

    loadData();
  }, []);

  if (loading) {
    return (
      <div style={{ textAlign: 'center', marginTop: '50px' }}>
        <Spin size="large" />
      </div>
    );
  }

  return (
    <div>
      <div className="page-header">
        <Title level={2} className="page-title">
          
        </Title>
        <p></p>
      </div>

      {/*  */}
      <Row gutter={[16, 16]} style={{ marginBottom: '24px' }}>
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title=""
              value={stats.totalMaterials}
              prefix={<ShoppingOutlined />}
              valueStyle={{ color: '#3f8600' }}
            />
          </Card>
        </Col>
        
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title=""
              value={stats.totalMenus}
              prefix={<MenuOutlined />}
              valueStyle={{ color: '#1890ff' }}
            />
          </Card>
        </Col>
        
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title=""
              value={30}
              prefix={<UserOutlined />}
              valueStyle={{ color: '#722ed1' }}
            />
          </Card>
        </Col>
        
        <Col xs={24} sm={12} lg={6}>
          <Card>
            <Statistic
              title=""
              value=""
              prefix={<TrophyOutlined />}
              valueStyle={{ color: '#52c41a' }}
            />
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Row gutter={[16, 16]}>
        <Col xs={24} lg={12}>
          <Card title="" size="small">
            <List
              dataSource={stats.recentMenus}
              renderItem={(menu) => (
                <List.Item>
                  <List.Item.Meta
                    title={menu.name}
                    description={`: ${menu.selling_price} | : ${new Date(menu.last_updated).toLocaleDateString('ja-JP')}`}
                  />
                </List.Item>
              )}
              locale={{ emptyText: '' }}
            />
          </Card>
        </Col>
        
        <Col xs={24} lg={12}>
          <Card title="" size="small">
            <List
              dataSource={stats.recentMaterials}
              renderItem={(material) => (
                <List.Item>
                  <List.Item.Meta
                    title={material.name}
                    description={`: ${material.unit_price}/${material.unit} | : ${new Date(material.last_updated).toLocaleDateString('ja-JP')}`}
                  />
                </List.Item>
              )}
              locale={{ emptyText: '' }}
            />
          </Card>
        </Col>
      </Row>

      {/*  */}
      <Row style={{ marginTop: '24px' }}>
        <Col span={24}>
          <Card title="" size="small">
            <Row gutter={[16, 16]}>
              <Col xs={24} md={8}>
                <div><strong>:</strong> 1.0.0</div>
              </Col>
              <Col xs={24} md={8}>
                <div><strong>:</strong> SQLite</div>
              </Col>
              <Col xs={24} md={8}>
                <div><strong>:</strong> 30</div>
              </Col>
            </Row>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default Dashboard; 